.modalIMG {
    width: 450px;
    height: 300px;
}

#viewport {
    margin-left: 15%;
    width: 100%;
    height: 500px;
    overflow: hidden;
}
 
.slider {
    position: relative;
    width: calc(100% * 4);
    height: 500px; 
    display: flex;
    justify-content: start;
    flex-wrap: nowrap;
}
 
.slide {
    width: 70%;
    height: 500px;
}
 
.slide img {
    width: 70%;
    height: 500px;
    object-fit: cover;
    object-position: 0;
    margin-left: auto;
    margin-right: auto;
}